@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

.body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  min-width: 375px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1500px;
  min-width: 375px;
}

.container-header {
  padding: 0 15px;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0px;
  padding: 0px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  border: none;
  display: inline-block;
}

.span-upper {
  text-transform: uppercase;
}

.container.about-container {
  padding: 0 30px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 60px;
  }
  .container.about-container {
    padding: 0 60px;
  }
}
.gradient {
  background: url(../img/back.svg) no-repeat center top, linear-gradient(208deg, #323264 0%, #323264 59.9%, #643E72 100%);
  border-radius: 0px 0px 12px 12px;
  position: relative;
  background-color: #323163;
}
.gradient::after {
  content: "";
  border-radius: 0px 0px 12px 12px;
  background: #CACADB;
  height: 10px;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: -10px;
}
.gradient::before {
  content: "";
  border-radius: 0px 0px 12px 12px;
  background: #E7E7F2;
  height: 10px;
  position: absolute;
  left: 40px;
  right: 40px;
  bottom: -20px;
}

@media (min-width: 768px) {
  .gradient {
    background-size: 893px 1024px, cover;
  }
}
@media (min-width: 992px) {
  .gradient {
    background-size: 670px 768px, cover;
  }
}
@media (min-width: 1200px) {
  .gradient {
    background-size: 697px 799px, cover;
  }
}
.header {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content min-content;
}
.header__info {
  grid-column: 1/-1;
  display: flex;
  justify-content: space-between;
  background: rgba(245, 245, 250, 0.2);
  margin: 0 -15px;
  padding: 10px 15px;
  align-items: center;
}
.header__info.about-info {
  background: #F5F5FA;
}
.header__adress, .header__fb {
  display: none;
}
.header__phone {
  color: #fff;
  font-size: 16px;
  line-height: normal;
}
.header__phone.about-phone {
  color: #B3B3BA;
}
.header__lang {
  display: flex;
  list-style: none;
  padding-left: 0;
}
.header__lang a {
  color: #fff;
  font-size: 12px;
  line-height: normal;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 5px 7px;
  border-radius: 8px;
  transition-duration: 0.4s;
  transition-property: background-color;
}
.header__lang a.active {
  background-color: #F0145A;
}
.header__lang a.active.about-active {
  color: #fff;
}
.header__lang.about-lang a {
  color: #B3B3BA;
}
.header__logo-box {
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 10px;
  gap: 4px;
}
.header__logo-box-img {
  display: block;
}
.header__logo-box-page {
  color: #fff;
  font-size: 36px;
  font-weight: 400;
  line-height: normal;
  display: none;
  align-items: center;
  gap: 4px;
}
.header__logo-box-page::before {
  content: "";
  background-image: url(../img/slash.svg);
  width: 21px;
  height: 47px;
}
.header__logo-box-page.about-page {
  display: flex;
  white-space: nowrap;
  padding-bottom: 7px;
  font-size: 20px;
  gap: 4px;
  color: #323264;
}
.header__logo-box-page.about-page::before {
  content: "";
  background-image: url(../img/slash-about.svg);
  width: 12px;
  height: 26px;
}

@media (min-width: 768px) {
  .header {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
  .header__info {
    justify-content: flex-start;
    gap: 6px;
    background: transparent;
    margin: 0;
    padding: 50px 0px 30px 0px;
  }
  .header__info.about-info {
    background-color: transparent;
  }
  .header__lang a:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  .header__lang.about-lang a:hover {
    background-color: #F5F5FA;
  }
  a.active:hover {
    background-color: #F0145A;
  }
  a.active.about-active:hover {
    background-color: #F0145A;
  }
  .header__adress,
  .header__fb {
    display: block;
    color: #B3B3BA;
    text-align: right;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
  }
  .header__adress {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
  }
  .header__adress.about-adress {
    color: #B3B3BA;
  }
  .header__adress::before {
    content: "";
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
  }
  .header__adress::after {
    content: "";
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
  }
  .header__fb {
    display: inline-flex;
    align-items: center;
    gap: 6px;
  }
  .header__fb::before {
    content: "";
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
  }
  .header__fb:hover .hover {
    fill: #F0145A;
    transition-duration: 0.8s;
    transition-property: fill;
  }
  .header__logo-box {
    padding-top: 0;
    padding-bottom: 30px;
  }
  .header__logo-box-img {
    width: 130px;
    height: auto;
  }
  .header__logo-box-page {
    font-size: 36px;
  }
  .header__logo-box-page.about-page {
    font-size: 36px;
  }
  .header__logo-box-page.about-page::before {
    content: "";
    background-image: url(../img/slash768.svg);
    width: 21px;
    height: 47px;
  }
  .header__logo-box-page::before {
    background-size: 21px 47px;
    width: 21px;
    height: 47px;
  }
}
@media (min-width: 992px) {
  .header {
    padding-top: 50px;
    grid-template-columns: min-content 1fr;
    grid-template-rows: min-content min-content;
  }
  .header__info {
    padding-top: 0;
    padding-bottom: 0;
    grid-column: 2/-1;
    justify-content: flex-end;
  }
  .header__logo-box {
    grid-row: 1/-1;
  }
}
.menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.menu__btn {
  background-image: url(../img/hamburger.svg);
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.menu__btn.about-btn {
  background-image: url(../img/hamburger-about.svg);
}
.menu__btn.active {
  background-image: url(../img/close.svg);
}
.menu__list {
  display: none;
}
.menu__burger {
  display: none;
  flex-direction: column;
  position: fixed;
  gap: 15px;
  list-style: none;
  padding-left: 0;
  top: 100px;
  left: 0;
  text-align: center;
  justify-content: center;
  font-size: 24px;
  width: 100%;
  border-radius: 12px;
  background-color: #fff;
  padding: 12px 0;
}
.menu .menu__burger-link {
  padding-top: 7px;
  padding-bottom: 7px;
  background-color: #F5F5FA;
  width: 351px;
  border-radius: 12px;
  color: #24A3FF;
  font-size: 24px;
}
.menu .bg {
  background-color: #E6E6F0;
}
.menu .red {
  color: #F0145A;
}

@media (min-width: 768px) {
  .menu {
    display: flex;
    justify-content: flex-start;
  }
  .menu__btn {
    display: none;
  }
  .menu__list {
    display: flex;
    gap: 15px;
    list-style: none;
    padding-left: 0;
  }
  .menu__list-link {
    color: #fff;
    font-size: 16px;
    border-bottom: 2px solid transparent;
    padding-bottom: 3px;
    transition-duration: 0.7s;
    transition-property: border-bottom;
  }
  .menu__list-link:hover {
    border-bottom: 2px solid #fff;
  }
  .about-menu .menu__list-link {
    color: #24A3FF;
    border-bottom: 2px solid transparent;
    transition-duration: 0.7s;
    transition-property: color, border-bottom;
  }
  .menu__list-link.red {
    color: #F0145A;
    border-bottom: 2px solid #F0145A;
  }
  .about-menu .menu__list-link:hover {
    color: #F0145A;
    border-bottom: 2px solid #F0145A;
  }
}
@media (min-width: 992px) {
  .menu {
    padding-top: 10px;
    align-items: flex-start;
    justify-self: end;
  }
  .menu.about-menu {
    padding-top: 16px;
  }
}
.solution {
  padding-top: 129px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.solution__title {
  color: #fff;
  text-align: center;
  font-size: 38px;
  padding-bottom: 159px;
}
.solution__wrap {
  display: flex;
  justify-content: center;
}
.solution__wrap-link {
  display: inline-flex;
  position: relative;
}
.solution__wrap-link:hover .circle {
  stroke: #F0145A;
  transition-duration: 1s;
}
.solution__wrap-link:hover .circle-path {
  stroke: #F0145A;
  animation-name: spinner;
  animation-delay: 0.4s;
  animation-duration: 1s;
  animation-iteration-count: 1s;
}
@keyframes spinner {
  100% {
    transform: rotate(360deg);
  }
}

@media (min-width: 768px) {
  .solution {
    padding-top: 269px;
  }
  .solution__title {
    padding-bottom: 417px;
  }
}
@media (min-width: 992px) {
  .solution {
    padding-top: 225px;
  }
  .solution__title {
    padding-bottom: 289px;
  }
}
@media (min-width: 1200px) {
  .solution {
    padding-top: 218px;
  }
  .solution__title {
    padding-bottom: 305px;
  }
}
.scroll {
  padding-top: 60px;
}
.scroll__switch {
  display: flex;
  gap: 30px;
  overflow: auto;
  list-style: none;
  padding-left: 0;
}
.scroll ::-webkit-scrollbar {
  height: 4px;
  background-color: rgba(255, 255, 255, 0.3);
}
.scroll ::-webkit-scrollbar-thumb {
  background-color: transparent;
}
.scroll ::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 144px;
}
.scroll__switch-item {
  white-space: nowrap;
  color: #B3B3BA;
  font-size: 30px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
  padding-bottom: 30px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
}
.scroll__switch-item.active {
  white-space: nowrap;
  font-size: 30px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  background: linear-gradient(118deg, #F0145A 0.84%, #EF8080 99.96%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.scroll__content {
  display: none;
}
.scroll__content.active {
  display: block;
}
.scroll__content-title {
  padding-top: 30px;
  color: #fff;
  font-size: 20px;
  padding-bottom: 30px;
}
.scroll__content-text {
  color: #fff;
  font-size: 19px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  padding-bottom: 30px;
}

@media (min-width: 768px) {
  .scroll {
    padding-top: 90px;
  }
  .scroll__switch-item {
    padding-bottom: 60px;
    font-size: 38px;
  }
  .scroll__switch-item.active {
    font-size: 38px;
  }
  .scroll__content-title {
    padding-top: 60px;
    font-size: 30px;
  }
  .scroll__content-text {
    padding-bottom: 60px;
    max-width: 648px;
  }
}
@media (min-width: 992px) {
