  .scroll__content-text {
    padding: 0px 0px 60px 0px;
  }
}
@media (min-width: 992px) {
  .scroll__content.active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
  }
  .scroll__content-title {
    padding-bottom: 0;
  }
  .scroll__content-text {
    padding-bottom: 0;
  }
}
